:root {
  --button-color: #00acdc;
  --background-color-main: #fff;
  --price-color: #616070;
  --cost-color: #060b35;
}

* {
  font-family: Roboto, sans-serif;
}

body {
  background-color: bisque;
  width: 100vw;
  height: 100vw;
}

.card {
  box-sizing: border-box;
  background-color: var(--background-color-main);
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  margin: 0 auto;
  padding-bottom: 16px;
  display: block;
}

.card__img {
  object-fit: contain;
  width: 160px;
  height: 134px;
  padding: 32px 19px 0;
  display: block;
}

.card__title {
  color: var(--cost-color);
  height: 36px;
  margin: 40px 16px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: var(--price-color);
  margin: 0;
  padding-top: 4px;
  padding-left: 16px;
  font-size: 10px;
  line-height: 14px;
  display: block;
}

.element {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.element:last-child {
  margin-right: 0;
}

.card__stars {
  width: min-content;
  display: flex;
}

.stars-1 .element:first-child, .stars-2 .element:nth-child(-n+2), .stars-3 .element:nth-child(-n+3), .stars-4 .element:nth-child(-n+4), .stars-5 .element:nth-child(n) {
  background-image: url("star-active.edda4c43.svg");
}

.card__rang {
  justify-content: space-between;
  width: 166px;
  padding: 16px 16px 0;
  display: flex;
}

.card__reviews {
  color: var(--cost-color);
  align-items: center;
  width: max-content;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__expense {
  justify-content: space-between;
  width: 166px;
  padding: 24px 16px 16px;
  display: flex;
}

.card__expense-price {
  color: var(--price-color);
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__expense-cost {
  color: var(--cost-color);
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: var(--background-color-main);
  text-transform: uppercase;
  background-color: var(--button-color);
  border: 1px solid var(--button-color);
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 0 auto;
  font-size: 14px;
  font-weight: 700;
  display: block;
}

.card__button:hover {
  color: var(--button-color);
  background-color: var(--background-color-main);
}

